import TodoItem from "./TodoItem";

const TodosList = (props) => {
  const { todos } = props;
  return (
    <ul>
      {(todos)? (todos.map(todo => (
        <TodoItem key={todo.id} todo={todo} handleChangeProps={props.handleChangeProps}  deleteTodoProps={props.deleteTodoProps} setUpdate={props.setUpdate}/>
      ))): (<TodoItem key={'first-Item'} todo={{id:0, title: 'add your item', completed: false}} handleChangeProps={props.handleChangeProps}  deleteTodoProps={props.deleteTodoProps} setUpdate={props.setUpdate}/>)}
    </ul>
  );
};

export default TodosList;